//@flow
import EventManager from './event/event-manager'
import FakeEventTarget from './event/fake-event-target'
import FakeEvent from './event/fake-event'
import PlayerEvents from './event/events'
import PlayerStates from './state/state-types'
import {isNumber, isFloat} from './utils/util'
import {capitlize} from './utils/string-util'
import LoggerFactory from './utils/logger'
import Html5 from './engines/html5/html5'
import PluginManager from './plugin/plugin-manager'
import StateManager from './state/state-manager'
import TrackTypes from './track/track-types'
import Track from './track/track'
import VideoTrack from './track/video-track'
import AudioTrack from './track/audio-track'
import TextTrack from './track/text-track'

let logger = LoggerFactory.getLogger('Player');
type ListenerType = (event: FakeEvent) => any;

class Player extends FakeEventTarget {
  _pluginManager: PluginManager;
  _eventManager: EventManager;
  _config: any;
  _engine: IEngine;
  _engineEventHandlers: Map<string, ListenerType>;
  _stateManager: StateManager;
  _tracks: Array<Track> = [];

  constructor(config: Object) {
    super();
    this._stateManager = new StateManager(this);
    this._pluginManager = new PluginManager();
    this._eventManager = new EventManager();
    this._engineEventHandlers = new Map();
    for (let playerEvent in PlayerEvents) {
      if (PlayerEvents.hasOwnProperty(playerEvent)) {
        this._engineEventHandlers.set(`onEngine${capitlize(PlayerEvents[playerEvent])}_`, (event) => {
          return this.dispatchEvent(event);
        });
      }
    }
    this._config = config || Player.defaultConfig_();
    this.loadPlugins(this._config);
    this.selectEngine(this._config);
    this.attachMedia();
    logger.info('player is ready!');
  }

  destroy() {
    this._engine.destroy();
    this._eventManager.destroy();
    this._pluginManager.destroy();
    this._stateManager.destroy();
    // this.engine_ = null;
    // this.eventManager_ = null;
    this._config = null;
    this._tracks = [];
  }

  static defaultConfig_() {
    return {};
  }

  loadPlugins(config: Object): void {
    let plugins = config.plugins;
    for (let name in plugins) {
      if (plugins.hasOwnProperty(name)) {
        this._pluginManager.load(name, this, plugins[name]);
      }
    }
  }

  selectEngine(config: Object) {
    if (config && config.sources) {
      let sources = config.sources;
      for (let i = 0; i < sources.length; i++) {
        if (Html5.canPlayType(sources[i].mimetype)) {
          this.loadEngine(sources[i], config);
          break;
        }
      }
    }
  }

  loadEngine(source: Object, config: Object) {
    this._engine = new Html5(source, config);
    if (config.preload === "auto") {
      this.load();
    }
  }

  attachMedia() {
    // Listen to all HTML5-defined events and trigger them on the player
    for (let playerEvent in PlayerEvents) {
      if (PlayerEvents.hasOwnProperty(playerEvent)) {
        const handler = this._engineEventHandlers.get(`onEngine${capitlize(PlayerEvents[playerEvent])}_`);
        if (handler) {
          this._eventManager.listen(this._engine, PlayerEvents[playerEvent], handler);
        }
      }
    }
  }

  /**
   * Returns the tracks according to the filter. if no filter given returns the all tracks.
   * @function getTracks
   * @param {string} [type] - a tracks filter, should be 'video', 'audio' or 'text'.
   * @returns {Array<Track>} - The parsed tracks.
   * @public
   */
  getTracks(type?: string): Array<Track> {
    return this._getTracksByType(type);
  }

  /**
   * Returns the tracks according to the filter. if no filter given returns the all tracks.
   * @function _getTracksByType
   * @param {string} [type] - a tracks filter, should be 'video', 'audio' or 'text'.
   * @returns {Array<Track>} - The parsed tracks.
   * @private
   */
  _getTracksByType(type?: string): Array<Track> {
    return !type ? this._tracks : this._tracks.filter((track: Track) => {
      if (type === TrackTypes.VIDEO) {
        return track instanceof VideoTrack;
      } else if (type === TrackTypes.AUDIO) {
        return track instanceof AudioTrack;
      } else if (type === TrackTypes.TEXT) {
        return track instanceof TextTrack;
      } else {
        return true;
      }
    });
  }

  /**
   * Select a track
   * @function selectTrack
   * @param {Track} track - the track to select
   * @returns {void}
   * @public
   */
  selectTrack(track: Track): void {
    let success = this._selectTrackByType(track);
    if (success) {
      this._markActiveTrack(track)
    }
  }

  /**
   * Select a track by type
   * @function _selectTrackByType
   * @param {Track} track - the track to select
   * @returns {boolean} - success
   * @private
   */
  _selectTrackByType(track: Track): boolean {
    if (track) {
      if (track instanceof VideoTrack) {
        return this._engine.selectVideoTrack(track);
      } else if (track instanceof AudioTrack) {
        return this._engine.selectAudioTrack(track);
      } else if (track instanceof TextTrack) {
        return this._engine.selectTextTrack(track);
      }
    }
    return false;
  }

  /**
   * Mark the selected track as active
   * @function _markActiveTrack
   * @param {Track} track - the track to mark
   * @returns {void}
   * @private
   */
  _markActiveTrack(track: Track) {
    let type;
    if (track instanceof VideoTrack) {
      type = TrackTypes.VIDEO;
    } else if (track instanceof AudioTrack) {
      type = TrackTypes.AUDIO;
    } else if (track instanceof TextTrack) {
      type = TrackTypes.TEXT;
    }
    if (type) {
      let tracks = this.getTracks(type);
      for (let i = 0; i < tracks.length; i++) {
        tracks[i].active = track.index === i;
      }
    }
  }

  /**
   * Get the player config
   * @returns {Object} - The player configuration
   */
  get config(): Object {
    return this._config;
  }

  //  <editor-fold desc="Playback Interface">
  /**
   * Start/resume playback
   * @returns {void}
   */
  play(): void {
    if (!this._engine.src) {
      this.load().then(() => {
        this._engine.play();
      })
    }
    this._engine.play();
  }

  /**
   * Pause playback
   * @returns {void}
   */
  pause(): void {
    return this._engine.pause();
  }

  /**
   * Load media
   * @returns {Promise<*>} - The load promise
   */
  load(): Promise<*> {
    return this._engine.load().then((data) => {
      this._tracks = data.tracks;
    });
  }

  /**
   * Set the current time in seconds
   * @param {Number} to - The number to set in seconds
   */
  set currentTime(to: number) {
    if (isNumber(to)) {
      let boundedTo = to;
      if (to < 0) {
        boundedTo = 0;
      }
      if (boundedTo > this._engine.duration) {
        boundedTo = this._engine.duration;
      }
      this._engine.currentTime = boundedTo;
    }
  }

  /**
   * Get the current time in seconds
   * @returns {Number} - The playback current time
   */
  get currentTime(): number {
    return this._engine.currentTime;
  }

  /**
   * /**
   * Get the duration in seconds
   * @returns {Number} - The playback duration
   */
  get duration(): number {
    return this._engine.duration;
  }

  /**
   * Set playback volume
   * @param {Number} vol - The volume to set
   */
  set volume(vol: number) {
    if (isFloat(vol)) {
      let boundedVol = vol;
      if (boundedVol < 0) {
        boundedVol = 0;
      }
      if (boundedVol > 1) {
        boundedVol = 1;
      }
      this._engine.volume = boundedVol;
    }
  }

  /**
   * Get playback volume
   * @returns {Number} - The playback volume
   */
  get volume(): number {
    return this._engine.volume;
  }

  // </editor-fold>

  // <editor-fold desc="State">
  ready() {
  }

  /**
   * Get paused state
   * @returns {boolean} - Whether the video is paused or not
   */
  get paused(): boolean {
    return this._engine.paused;
  }

  /**
   * Get seeking state
   * @returns {boolean} - Whether the video is seeking or not
   */
  get seeking(): boolean {
    return this._engine.seeking;
  }

  buffered() {
  }

  /**
   * Set player muted state
   * @param {boolean} mute - The mute value
   */
  set muted(mute: boolean) {
    this._engine.muted = mute;
  }

  /**
   * Get player muted state
   * @returns {boolean} - Whether the video is muted or not
   */
  get muted(): boolean {
    return this._engine.muted;
  }

  /**
   * Get the player source
   * @returns {string} - The current source of the player
   */
  get src(): string {
    return this._engine.src;
  }

  /**
   * Get the player events
   * @returns {Object} - The events of the player
   */
  get Event(): { [event: string]: string } {
    return PlayerEvents;
  }

  /**
   * Get the player states
   * @returns {Object} - The states of the player
   */
  get State(): { [state: string]: string } {
    return PlayerStates;
  }

  /**
   * Get the player tracks types
   * @returns {Object} - The tracks types of the player
   */
  get Track(): { [track: string]: string } {
    return TrackTypes;
  }

// </editor-fold>
}

export default Player;



